.top-nav{
    width: 96vw;
    display: flex;
    justify-content: space-between;
    padding-top: 2vh;
    /* height: 4vh; */
    padding-left: 1vw;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    box-shadow: 0px 3px 8px #00000029;
    opacity: 1;
  }
  .right{
    /* float: right; */
    display: inline-flex;
    gap: 1rem;

  
  }
  
  .picture-nav{
   height: 5.1vh;
    width: 2.5vw;
    
    margin-top: 0vh;
    border-radius: 50px; 
    /* border: 1px solid black; */
    background-color: rgb(52, 183, 216);
     background-image: url('data:image/png;base64,') 0% 0% no-repeat padding-box; 
  }
  .select{
    background-color: red;
    color: white;
    border: 0px;
    margin-right: 25px;
    margin-top: 3px;
    padding: 5px;
  }
  .select:hover{
    cursor: pointer;
    background-color: black;
  }
.logoimg{
    margin-top: -7px;
    padding-bottom: 10px;
  }

  /* left nav */
  .left-nav{
    width:3.7%;
    padding-top: 40vh;
    background-color: #278DF1;
    /* height: 10%; */
  
  }
  .left-nav span{
    text-decoration: none;
    display: list-item;
    padding-top: 2vh;
    padding-left: 0.5vw;
  }